"use client"

import React, { useEffect, useRef, useState } from 'react'
import { useRouter } from 'next/navigation'

type Pet = {
  id: string
  nome?: string
  status?: string
  fotos_urls?: string[]
  localizacao?: {
    latitude?: number
    longitude?: number
  }
  photoUrl?: string
}

const STATUS_COLORS: Record<string, string> = {
  lost: '#ef4444',
  found: '#3b82f6',
  adoption: '#10b981',
  default: '#6b7280'
}

function loadGoogleMaps(apiKey: string) {
  return new Promise<void>((resolve, reject) => {
    if (typeof window === 'undefined') return reject(new Error('window undefined'))
    if ((window as any).google && (window as any).google.maps) return resolve()

    const id = 'google-maps-script'
    if (document.getElementById(id)) {
      // wait for it to be ready
      const check = setInterval(() => {
        if ((window as any).google && (window as any).google.maps) {
          clearInterval(check)
          resolve()
        }
      }, 100)
      return
    }

    const script = document.createElement('script')
    script.id = id
    script.src = `https://maps.googleapis.com/maps/api/js?key=${apiKey}&v=weekly`
    script.async = true
    script.defer = true
    script.onload = () => resolve()
    script.onerror = (e) => reject(e)
    document.head.appendChild(script)
  })
}

export default function GoogleMap({
  pets = [],
  selectedPetId,
  onPetSelect,
  statusFilter = 'all'
}: {
  pets?: Pet[]
  selectedPetId?: string | null
  onPetSelect?: (p: Pet) => void
  statusFilter?: 'all' | 'lost' | 'found' | 'adoption'
}) {
  const mapRef = useRef<HTMLDivElement | null>(null)
  const mapInstance = useRef<any>(null)
  const markersRef = useRef<any[]>([])
  const router = useRouter()
  const apiKey = process.env.NEXT_PUBLIC_GOOGLE_MAPS_API_KEY || ''
  const [legendOpen, setLegendOpen] = useState(false)

  useEffect(() => {
    if (!apiKey) {
      console.error('NEXT_PUBLIC_GOOGLE_MAPS_API_KEY não fornecida')
      return
    }

    let mounted = true
    loadGoogleMaps(apiKey)
      .then(() => {
        if (!mounted) return
        if (!mapRef.current) return

        // init map if not already
        if (!mapInstance.current) {
          mapInstance.current = new (window as any).google.maps.Map(mapRef.current, {
            center: { lat: -23.55, lng: -46.63 },
            zoom: 12,
            mapTypeControl: false,
            streetViewControl: false,
            fullscreenControl: false
          })
        }

        // initial markers render handled in separate effect
      })
      .catch((e) => console.error('Erro ao carregar Google Maps', e))

    return () => { mounted = false }
  }, [apiKey])

  // Build markers when pets or filter changes
  useEffect(() => {
    const map = mapInstance.current
    if (!map) return

    // clear existing markers
    markersRef.current.forEach(m => m.marker && m.marker.setMap(null))
    markersRef.current = []

    const normalized = (pets || [])
      .map(p => {
        // try multiple possible shapes for coordinates and coerce to numbers
        const tryVals = (v: any) => {
          if (v == null) return null
          if (typeof v === 'number') return v
          if (typeof v === 'string') {
            const n = parseFloat(v)
            return Number.isFinite(n) ? n : null
          }
          return null
        }

        const latCandidates = [
          p?.location?.lat,
          p?.location?.latitude,
          p?.localizacao?.latitude,
          p?.localizacao?.lat,
          (p as any)?.lat,
        ]
        const lngCandidates = [
          p?.location?.lng,
          p?.location?.longitude,
          p?.localizacao?.longitude,
          p?.localizacao?.lng,
          (p as any)?.lng,
        ]

        let lat: number | null = null
        let lng: number | null = null

        for (const c of latCandidates) {
          const v = tryVals(c)
          if (v != null) { lat = v; break }
        }
        for (const c of lngCandidates) {
          const v = tryVals(c)
          if (v != null) { lng = v; break }
        }

        return {
          id: p.id,
          nome: (p as any).name || (p as any).nome || 'Pet',
          lat,
          lng,
          photo: Array.isArray((p as any).fotos_urls) && (p as any).fotos_urls.length ? (p as any).fotos_urls[0] : (p as any).photoUrl,
          raw: p,
          status: p.status
        }
      })
      .filter(m => typeof m.lat === 'number' && typeof m.lng === 'number')
      .filter(m => {
        if (statusFilter === 'all') return true
        if (statusFilter === 'lost') return m.status === 'lost'
        if (statusFilter === 'found') return m.status === 'found'
        if (statusFilter === 'adoption') return m.status === 'adoption'
        return true
      })

    // debug: inspecionar normalização dos pets para confirmar lat/lng
    try { console.debug('[GoogleMap] normalized markers:', normalized) } catch (e) {}

    // create markers
    normalized.forEach(m => {
      const color = STATUS_COLORS[m.status || 'default'] || STATUS_COLORS.default
      const svgContent = '<svg xmlns="http://www.w3.org/2000/svg" width="36" height="36" viewBox="0 0 36 36">' +
        '<path d="M18 2 C12 2 8 6 8 12 C8 20 18 32 18 32 C18 32 28 20 28 12 C28 6 24 2 18 2 Z" fill="' + color + '" stroke="#ffffff" stroke-width="1.2"/>' +
        '<circle cx="18" cy="12" r="4" fill="#ffffff" />' +
        '</svg>'

      const svg = 'data:image/svg+xml;utf8,' + encodeURIComponent(svgContent)

      const marker = new (window as any).google.maps.Marker({
        position: { lat: Number(m.lat), lng: Number(m.lng) },
        map,
        title: m.nome,
        icon: {
          url: svg,
          scaledSize: new (window as any).google.maps.Size(36, 36),
          anchor: new (window as any).google.maps.Point(18, 34)
        }
      })

      // create a DOM node for the info window to avoid template parsing issues
      const contentNode = document.createElement('div')
      contentNode.style.maxWidth = '240px'

      const titleEl = document.createElement('strong')
      titleEl.textContent = m.nome
      contentNode.appendChild(titleEl)

      const racaEl = document.createElement('div')
      racaEl.style.marginTop = '6px'
      racaEl.textContent = (m.raw && (m.raw as any).raca) || ''
      contentNode.appendChild(racaEl)

      const actionWrap = document.createElement('div')
      actionWrap.style.textAlign = 'right'
      actionWrap.style.marginTop = '8px'
      const btn = document.createElement('button')
      btn.setAttribute('data-id', m.id)
      btn.style.background = '#0ea5a4'
      btn.style.color = '#fff'
      btn.style.padding = '6px 8px'
      btn.style.border = 'none'
      btn.style.borderRadius = '6px'
      btn.style.cursor = 'pointer'
      btn.textContent = 'Ver mais'
      actionWrap.appendChild(btn)
      contentNode.appendChild(actionWrap)

      const infoWindow = new (window as any).google.maps.InfoWindow({ content: contentNode })

      marker.addListener('click', () => {
        infoWindow.open(map, marker)
      })

      // delegate click on button
      btn.onclick = () => {
        try {
          router.push(`/pet/${m.id}`);
        } catch (e) {
          window.location.href = `/pet/${m.id}`;
        }
      }

      markersRef.current.push({ id: m.id, marker, infoWindow })
    })

    // If no markers were created from data, add a temporary test marker so
    // developer can confirm marker rendering works. This is safe to keep
    // temporarily and will not affect production data.
    if (markersRef.current.length === 0) {
      try {
        const testPos = { lat: -23.55, lng: -46.63 }
        const testSvgContent = '<svg xmlns="http://www.w3.org/2000/svg" width="36" height="36" viewBox="0 0 36 36">' +
          '<path d="M18 2 C12 2 8 6 8 12 C8 20 18 32 18 32 C18 32 28 20 28 12 C28 6 24 2 18 2 Z" fill="#ff00aa" stroke="#ffffff" stroke-width="1.2"/>' +
          '<circle cx="18" cy="12" r="4" fill="#ffffff" />' +
          '</svg>'
        const testIcon = 'data:image/svg+xml;utf8,' + encodeURIComponent(testSvgContent)

        const testMarker = new (window as any).google.maps.Marker({
          position: testPos,
          map,
          title: 'Marcador de teste',
          icon: {
            url: testIcon,
            scaledSize: new (window as any).google.maps.Size(36, 36),
            anchor: new (window as any).google.maps.Point(18, 34)
          }
        })

        const info = new (window as any).google.maps.InfoWindow({ content: '<div style="max-width:200px">Marcador de teste (remova depois)</div>' })
        testMarker.addListener('click', () => info.open(map, testMarker))
        markersRef.current.push({ id: 'test-marker', marker: testMarker, infoWindow: info })
        try { console.info('[GoogleMap] marcador de teste adicionado') } catch (e) {}
      } catch (e) {
        // ignore any errors when adding test marker
      }
    }
    // center map if selectedPetId present
    if (selectedPetId) {
      const found = markersRef.current.find(x => x.id === selectedPetId)
      if (found) {
        map.panTo(found.marker.getPosition())
        map.setZoom(14)
        // open info window
        found.infoWindow.open(map, found.marker)
      }
    } else if (markersRef.current.length) {
      // fit bounds
      const bounds = new (window as any).google.maps.LatLngBounds()
      markersRef.current.forEach(m => bounds.extend(m.marker.getPosition()))
      map.fitBounds(bounds)
    }

  }, [pets, statusFilter, selectedPetId])

  return (
    <div className="w-full h-full relative">
      <div ref={mapRef} style={{ width: '100%', height: '100%', minHeight: 360 }} />

      <div className="absolute top-3 right-3 z-50 text-sm">
        <div className="hidden sm:block bg-white/95 text-gray-900 rounded shadow-md p-2 pointer-events-auto border border-gray-200">
          <div className="flex flex-col gap-1">
            <div className="flex items-center gap-2"><span className="inline-block w-4 h-4" style={{ background: STATUS_COLORS.lost }} /> Perdido</div>
            <div className="flex items-center gap-2"><span className="inline-block w-4 h-4" style={{ background: STATUS_COLORS.found }} /> Encontrado</div>
            <div className="flex items-center gap-2"><span className="inline-block w-4 h-4" style={{ background: STATUS_COLORS.adoption }} /> Adoção</div>
          </div>
        </div>
        <div className="sm:hidden">
          <button aria-label="Legenda" onClick={() => setLegendOpen(v => !v)} className="bg-white/95 text-gray-900 p-2 rounded-full shadow-md border border-gray-200">
            <svg width="18" height="18" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
              <circle cx="6" cy="6" r="3" fill="#ef4444" />
              <circle cx="12" cy="6" r="3" fill="#3b82f6" />
              <circle cx="18" cy="6" r="3" fill="#10b981" />
            </svg>
          </button>
        </div>
      </div>

      {legendOpen && (
        <div className="absolute bottom-4 right-4 z-50 bg-white/95 text-gray-900 rounded shadow-md p-3 w-40 border border-gray-200">
          <div className="flex flex-col gap-2">
            <div className="flex justify-end"><button onClick={() => setLegendOpen(false)} className="text-xs text-gray-600">Fechar</button></div>
            <div className="flex items-center gap-2"><span className="inline-block w-4 h-4" style={{ background: STATUS_COLORS.lost }} /> Perdido</div>
            <div className="flex items-center gap-2"><span className="inline-block w-4 h-4" style={{ background: STATUS_COLORS.found }} /> Encontrado</div>
            <div className="flex items-center gap-2"><span className="inline-block w-4 h-4" style={{ background: STATUS_COLORS.adoption }} /> Adoção</div>
          </div>
        </div>
      )}
    </div>
  )
}
